<template>
    <ul id="example-2">
      <li v-for="(item, index) in arrList">

          <router-link  :to="{ name: 'Group'}">
            <h1>{{item.name}}</h1>
          </router-link>
          <h1>{{item.cur}}</h1>
          <h1>已上映：{{item.days}}天</h1>
          <h1>票房：{{item.sum}}万</h1>

      </li>
    </ul>

</template>

<script>
  export default {
      name:'example-2',
    data(){
      return{
        arrList:[]
      }
    },
    mounted(){
      this.fetchData();
    },
    methods:{
      fetchData(){
        var _this=this;
        this.$http.get('https://mobapi.ibenchu.pw/boxoffice/day/query',{
          params: {
            key: '1eae6b9688738',
            area:'CN'
          }})
          .then(function (res) {

            _this.arrList=res.data.result;
          console.log( res.data.result)
//            console.log( _this.arrList)
          })
          .catch(function (error) {
            console.log(error);
          })

      }
    }
  }
</script>

<style scoped="scoped">
  #example-2{
    margin-top: .17rem;
    width: 100%;
    overflow: auto;
  }
  li{
    padding: .1rem .05rem;
    position: relative;
  }
  li::after{
    content: "";
    height:1px;
    border-bottom:1px solid #000000;
    width:97%;
    position:absolute;
    transform:scaleY(0.5) ;
    bottom:0px;
  }

</style>



