<template>
  <div id="nowintheater">
    <h1>影院热映</h1>
    <section id="list">
      <a v-for="(item, index) in arrList" href="">

      </a>
    </section>

  </div>
</template>
<script>
  import $ from 'jquery'
  export default {
    data(){
      return{
        arrList:[]
      }
    },
    mounted(){
      this.fetchData();
    },
    methods:{
      fetchData(){
        //将所请求的数据放在static文件夹中
        var _this=this
//        this.$http.get("https://api.douban.com/v2/movie/top250").then(function (res) {
//          console.log(res)
//          _this.arrList=res.data
//        }).catch(function (error) {
//          console.log(error);
//        });
        $.ajax({
          url:"https://api.douban.com/v2/movie/top250",
          dataType:'jsonp',
          data:'',
          jsonp:'callback',
          success:function(res) {
            console.log(res.subjects)
            _this.arrList=res.subjects
          }
        });

      }
    }
  }
</script>
<style scoped>
  #nowintheater{
    padding: 0 2%;
    height: 100%;
  }
  h1{
    font-size: 24px;
    font-weight: normal;
    box-sizing: border-box;
    max-width: 660px;
    color: #494949;
    margin-top: .16rem;
    margin-bottom: 6px;
    padding-left: 4%;
  }

</style>



