<script type='text/ecmascript-6'>
  import $ from 'jquery'
    export default {
        data(){
            return {
              arrList: {}
            }
        },
        mounted(){
            var reg=/\/detail\/(\d+)/
          console.log(this.$route.path)
            var id = this.$route.path.match(reg)[1]
//          console.log(this.$route.path.match(reg)[1])
//            console.log(this.$route.path)
            this.fetchData(id)


        },
        methods:{
          fetchData(id){
              var _this=this
              this.$http.get("../../static/data/home.data").then(function(res){

                 _this.arrList=res.data[id]
                $(document).ready(function(){
                  var score = $('#score').html()
                  var t=(score/2).toFixed(0)
                  for (var i=0 ;i<t;i++){
                    $('.score li').eq(i).css('background-position','0 -.24rem')
                  }
                })

              }).catch(function (error) {
                console.log(error)
              })
          }
        }
    }
</script>

