import Home from './components/home.vue'
import Movie from './components/movie.vue'
import Books from './components/books.vue'
import Broadcast from './components/broadcast.vue'
import Group from './components/group.vue'
import Detail from './components/detail.vue'
import Nowintheater from './components/nowintheater.vue'
//console.log(Home)
export default[
    {
      path:'/nowintheater',
      name:'Nowintheater',
      component:Nowintheater
    },
    {
        path:'/home',
        name:'Home',
        component:Home
    },
    {
        path:'/movie',
        name:'Movie',
        component:Movie
    },
    {
        path:'/books',
        name:'Books',
        component:Books
    },
    {
      path:'/group',
      name:'Group',
      component:Group
    },
    {
      path:'/detail/:index',
      name:'Detail',
      component:Detail
    },
    {
      path:'/broadcast',
      name:'Broadcast',
      component:Broadcast
    },
    {
      path:'/',
      redirect:'/home'
    },
    {
      path:'*',
      redirect:'/home'
    }

]




